/**
 * BASE
 */

html,
body {
	height: 100%;
	width: 100%;
}

html {
	box-sizing: border-box;
	font-size: 62.5%;
}

body {
	margin: 0;
	counter-reset: a;
	font-family: "Cabin", sans-serif;
}

h1,
.h1 {
	font-family: "NovecentoWide", sans-serif;
	font-size: 4rem;
}

h2,
.h2 {
	font-family: "NovecentoWide", sans-serif;
	font-size: 3rem;
}

*,
*::after,
*::before {
	box-sizing: inherit;
}

a {
	color: currentColor;
	cursor: pointer;
	text-decoration: none;
}

/**
 * UTILITIES
 */

.uppercase {
	text-transform: uppercase;
}

.text-left {
	text-align: left;
}

.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.font-light {
	font-weight: 300;
}

.font-normal {
	font-weight: 400;
}

.font-medium {
	font-weight: 500;
}

.font-bold {
	font-weight: 700;
}

.color-white {
	color: #fff;
}

.color-black {
	color: #000;
}

.color-red {
	color: #c13b27;
}

.color-grey {
	color: #7c7a7b;
}

.desaturate {
	filter: gray;
	filter: grayscale(100%);
}

.height-100 {
	height: 100%;
}

.display-table {
	display: table;
}

.display-table-cell {
	display: table-cell;
	vertical-align: middle;
}

.display-block {
	display: block;
}

.display-none {
	display: none;
}

.display-inline-block {
	display: inline-block;
}

.valign-middle {
	vertical-align: middle;
}

.overflow-hidden {
	overflow: hidden;
}

.visually-hidden {
	visibility: hidden;
}

.opacity-none {
	opacity: 0;
}

.fluid {
	display: block;
	width: 100%;
	height: auto;
}

.clearfix::after,
.clearfix::before {
	content: "";
	display: table;
}

.clearfix::after {
	clear: both;
}

.float-left {
	float: left;
}

.float-right {
	float: right;
}

.position-fixed {
	position: fixed;
}

.position-relative {
	position: relative;
}

.position-absolute {
	position: absolute;
}

.margin-center {
	margin: 0 auto;
}

.background-white {
	background-color: #fff;
}

.border-white {
	border-color: #fff;
}

.background-cover {
	background-size: cover;
	background-position: center;
}

.background-dark-grey {
	background-color: #363334;
}

.background-light-grey {
	background-color: #f3f3f3;
}

.menu-inline {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.menu-inline li {
	display: inline-block;
}

.embed-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;
}

.embed-container > iframe,
.embed-container > object,
.embed-container > embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

/**
 * COMPONENTS
 */

.menu-item {
	color: #fff;
}

.menu-item.current-menu-item > a {
	border: 1px solid currentColor;
}

.menu-item > a {
	padding: 0.5rem 1rem;
	border: 1px solid transparent;
	border-radius: 1px;
	color: currentColor;
	font-size: 1.4rem;
	text-transform: uppercase;
	transition: 0.3s ease;
}

.menu-item > a:hover {
	border: 1px solid currentColor;
	transition: 0.3s ease;
}

/**
 * COMPONENTS: ANIMATIONS
 */

.grayscale {
	filter: grayscale(100%);
	transition: 0.3s ease;
}

.grayscale:hover {
	filter: grayscale(0);
	transition: 0.3s ease;
}

.hoverable {
	position: relative;
}

.hoverable::after {
	display: block;
	height: 2px;
	width: 0;
	position: absolute;
	bottom: -7px;
	left: auto;
	right: 0;
	background-color: #c13b27;
	content: "";
	transition: width .3s ease;
}

.hoverable:hover::after {
	width: 100%;
	left: 0;
	right: auto;
}

/**
 * LAYOUT
 */

.inner {
	height: 100%;
	width: 100%;
	margin: 0 auto;
	max-width: 1024px;
}

.inner-wrapper {
	margin: 0 35px;
}

.inner.has-padding {
	padding-bottom: 14rem;
}

.inner.is-last {
	padding-bottom: 28.5rem;
}

.oblic-rtl {
	position: relative;
}

.oblic-rtl::before {
	display: block;
	position: absolute;
	top: -144px;
	height: 144px;
	width: 100%;
	background-color: inherit;
	content: "";
	clip-path: polygon(0% 100%, 100% 0%, 100% 100%, 0% 100%);
}

.oblic-ltr {
	position: relative;
}

.oblic-ltr::before {
	display: block;
	position: absolute;
	top: -144px;
	height: 144px;
	width: 100%;
	background-color: inherit;
	content: "";
	clip-path: polygon(0% 0%, 100% 100%, 100% 100%, 0% 100%);
}

.site-section__title {
	color: currentColor;
	position: relative;
	margin-bottom: 6rem;
	margin-top: 0;
	text-transform: uppercase;
}

.site-section__title::after {
	display: block;
	height: 0.4rem;
	width: 8.5rem;
	position: absolute;
	left: 50%;
	bottom: -6rem;
	background-color: #c13b27;
	content: "";
	transform: translate(-50%);
}

.site-section__title::before {
	display: block;
	color: #7c7a7b;
	content: counter(a, decimal-leading-zero);
	counter-increment: a 1;
	font-family: "Cabin", sans-serif;
	margin-bottom: 3rem;
	margin-top: 1rem;
}

.site-section.entreprise .entreprise__description {
	margin: 1.7rem 0 0 0;
	color: #363334;
	font-size: 1.5rem;
}

.site-section .text-20 {
	font-size: 2rem;
	line-height: 2.6rem;
}

.site-section .text-20 .color-red {
	color: #c13b27;
}

.site-section .text-16 {
	font-size: 1.6rem;
	line-height: 2.6rem;
}

.site-section .text-16 .color-red {
	color: #c13b27;
}

.insert {
	position: absolute;
	left: 0;
	right: 0;
	top: -200px;
}

.insert__inner {
	background-color: #fff;
	border: 1px solid #363334;
}

.insert__title {
	position: absolute;
	right: 50%;
	padding: 1.2rem 1rem;
	background-color: #363334;
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.8rem;
	transform: translate(50%, -50%);
}

.insert__content {
	margin: 4rem 0 1.7rem 0;
}

.insert__content__list {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

.insert__content__list__item {
	margin: 0.7rem 0;
	font-size: 1.6rem;
}

.insert__content__list__item::before {
	display: inline-block;
	color: #c13b27;
	content: "- ";
}

.insert__content__list__item::after {
	display: inline-block;
	color: #c13b27;
	content: " -";
}

.products__title {
	margin: 0;
	font-family: "NovecentoWide", sans-serif;
	font-size: 2.5rem;
}

.products__link {
	display: inline-block;
	margin: 2rem 0 7rem 0;
	font-family: "NovecentoWide", sans-serif;
	font-size: 1.6rem;
}

.products__img {
	margin-bottom: 3rem;
	max-width: 100%;
}

.brands {
	margin: 1.5rem 0;
}

/**
 * HEADER
 */

.site-header {
	height: 76.4rem;
	position: relative;
	background-attachment: fixed;
	background-image: url("../../img//ecoflam__home.jpg");
	background-position: center;
	background-size: cover;
}

.site-header .inner {
	padding-top: 4rem;
}

.site-header__content {
	color: #fff;
	background-image: url("../../img//svg/ecoflam__background-title.svg");
	background-position: center;
	background-repeat: no-repeat;
}

.site-header__content__title > span {
	display: block;
	font-family: "Cabin", sans-serif;
	font-size: 3rem;
	font-style: italic;
}

.site-header__content__title > span:nth-child(1) {
	margin-bottom: 4rem;
	margin-top: 20rem;
}

.site-header__content__title > span:nth-child(2) {
	margin-top: 4rem;
	margin-bottom: 20rem;
}

@media (max-width: 479px) {

html {
	font-size: 46.875%;
}

}

